/* Other */

.hover\:cursor-pointer:hover{
  cursor: pointer;
}

@media (min-width: 768px){
  .md\:inline{
    display: inline;
  }

  .md\:h-10{
    height: 2.5rem;
  }

  .md\:h-12{
    height: 3rem;
  }

  .md\:w-10{
    width: 2.5rem;
  }

  .md\:p-6{
    padding: 1.5rem;
  }

  .md\:px-3{
    padding-left: 0.75rem;
    padding-right: 0.75rem;
  }

  .md\:text-6xl{
    font-size: 3.75rem;
    line-height: 1;
  }

  .md\:text-3xl{
    font-size: 1.875rem;
    line-height: 2.25rem;
  }

  .md\:text-2xl{
    font-size: 1.5rem;
    line-height: 2rem;
  }

  .md\:text-5xl{
    font-size: 3rem;
    line-height: 1;
  }
}

@media (min-width: 1024px){
  .lg\:absolute{
    position: absolute;
  }

  .lg\:top-4{
    top: 1rem;
  }

  .lg\:mx-10{
    margin-left: 2.5rem;
    margin-right: 2.5rem;
  }

  .lg\:mb-0{
    margin-bottom: 0px;
  }

  .lg\:h-12{
    height: 3rem;
  }

  .lg\:h-16{
    height: 4rem;
  }

  .lg\:w-12{
    width: 3rem;
  }

  .lg\:flex-col{
    flex-direction: column;
  }

  .lg\:border-0{
    border-width: 0px;
  }
}
